.take-points-fade {
  height: 2rem;
  font-family: "Lato", sans-serif;
  font-size: 2.6rem;
  color: red;
  margin: 0;
  left: 50%;
  top: 7%;
  position: absolute;
  animation: fade 1.5s linear;
  animation-fill-mode: forwards;
}

.add-points-fade {
  height: 3rem;
  font-family: "Lato", sans-serif;
  font-size: 2.6rem;
  color: green;
  margin: 0;
  left: 50%;
  top: 7%;
  position: absolute;
  animation: fade 1.5s linear;
  animation-fill-mode: forwards;
}

@keyframes fade {
  0%, 100% {opacity: 0}
  50% {opacity: 1}
  100% {transform: translatex(45px) translatey(-45px)}
}


